import { connect } from 'react-redux';
import Goodies from '../../Components/Main/Goodies/Goodies';
import { fetchGoodies } from '../../Redux/actions/listGoodies.actions';
import { fetchCollections } from '../../Redux/actions/listCollections.actions';
import { fetchTypes } from '../../Redux/actions/listTypes.actions';

const mapState = (state) => {
    return {
        goodies: state.listGoodiesReducer.goodies,
        collections: state.listCollectionsReducer.collections,
        types: state.listTypesReducer.types,
        errorMessage: state.listGoodiesReducer.errorMessage,
    };
};

const mapDispatch = (dispatch) => ({
    fetchGoodies: () => {
        dispatch(fetchGoodies());
    },
    fetchCollections: () => {
        dispatch(fetchCollections());
    },
    fetchTypes: () => {
        dispatch(fetchTypes());
    },
});

export default connect(mapState, mapDispatch)(Goodies);