import React, { useEffect } from 'react';
import Loading from '../../UIElement/Loading/Loading';

import './stylesEvents.scss';

const Events = ({ fetchEvents, events, errorMessage }) => {
  useEffect(() => {
    fetchEvents();
  }, [fetchEvents]);

  const convertToNiceDate = (dateString) => {
    const date = new Date(dateString);
    return `${date.getDate()}-${date.getMonth() + 1}-${date.getFullYear()}`;
  };

  const convertToLink = (address) => {
    const addressToCheck = address;
    if (addressToCheck.includes('http')) {
      return (
        <a
          href={addressToCheck}
          target="_blank"
          title="Un nouvel onglet sera ouvert"
          rel="noopener noreferrer"
        >
          Lien Discord
        </a>
      );
    } else {
      return addressToCheck;
    }
  };

  if (events.length === 0) {
    return <Loading />;
  } else {
    return (
      <>
        <h1>vnements</h1>
        <div className="events">
          {events.map((event) => (
            <div className="event" key={event.id}>
              <h2>{event.name}</h2>
              <div className="event-details">
                <img
                  className="event-details-poster"
                  src={`https://loupargent-oclock.fr/img/${event.picture_file}`}
                  alt=""
                />
                <div className="event-details-information">
                  <p>{event.location}</p>
                  <p>
                    {convertToNiceDate(event.start_date)} /{' '}
                    {convertToNiceDate(event.end_date)}
                  </p>
                  <p>{convertToLink(event.address)}</p>
                </div>
              </div>
            </div>
          ))}
        </div>
      </>
    );
  }
};

export default Events;
