import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import Loading from '../../UIElement/Loading/Loading';

import LogoFantasy from '../../../assets/img/collection_fantasy.png';
import LogoJeunesse from '../../../assets/img/collection_jeunesse.png';
import LogoSF from '../../../assets/img/collection_sf.png';
import LogoUrban from '../../../assets/img/collection_urban.png';

import './stylesBooks.scss';

const Books = ({
  fetchBooks,
  books,
  fetchTags,
  tags,
  fetchEditions,
  editions,
  fetchCollections,
  collections,
  errorMessage,
}) => {
  useEffect(() => {
    fetchBooks();
    fetchTags();
    fetchEditions();
    fetchCollections();
  }, [fetchBooks, fetchTags, fetchEditions, fetchCollections]);

  const getEditionPicture = (bookIdEdition) => {

  const edition = editions.find(edition => edition.id === bookIdEdition);

    if (edition !== undefined) {
      return `https://loupargent-oclock.fr/img/${edition.picture_file}`;
    }
  };

  const getEditionName = (bookIdEdition) => {
    
  const edition = editions.find(edition => edition.id === bookIdEdition);


    if (edition !== undefined) {
      return edition.name;
    }
  };

  if (books.length === 0) {
    return <Loading />;
  } else {
    return (
      <div>
        <div className="filter-zone">
          <h3>Filtrer par : </h3>

          <div className="filters">
            <p className="filter">
              <span>COLLECTION : </span>
              <img className="gender-logo" src={LogoFantasy} alt="" />
              <img className="gender-logo" src={LogoJeunesse} alt="" />
              <img className="gender-logo" src={LogoSF} alt="" />
              <img className="gender-logo" src={LogoUrban} alt="" />
            </p>

            <p className="filter">
              <span>GENRE : </span>
              <img className="gender-logo" src={LogoFantasy} alt="" />
              <img className="gender-logo" src={LogoJeunesse} alt="" />
              <img className="gender-logo" src={LogoSF} alt="" />
              <img className="gender-logo" src={LogoUrban} alt="" />
            </p>

            <p className="filter">
              <span>SOUS-GENRE : </span>
              <img className="gender-logo" src={LogoFantasy} alt="" />
              <img className="gender-logo" src={LogoJeunesse} alt="" />
              <img className="gender-logo" src={LogoSF} alt="" />
              <img className="gender-logo" src={LogoUrban} alt="" />
            </p>
          </div>
        </div>

        <div className="novels">
          {books.map((book) => (
            <div className="novel" key={book.id}>
              <div className="novel-header">
                <Link to={`/book/${book.id}`}>
                  <h3 className="novel-title">{book.name}</h3>
                </Link>
              </div>
              <div className="novel-main">
                <img
                  className="novel-main-picture"
                  src={`https://loupargent-oclock.fr/img/${book.picture_file[0]}`}
                  alt={book.name}
                />
                <img
                  className="novel-gender"
                  src={getEditionPicture(book.id_edition)}
                  alt={getEditionName(book.id_edition)}
                />
              </div>
              <div className="novel-alt">
                {book.tag.map((oneTag) => (
                  <img
                    key={oneTag[0]}
                    className="novel-alt-picture"
                    src={`https://loupargent-oclock.fr/img/${oneTag[2]}`}
                    alt={oneTag[1]}
                    title={oneTag[1]}
                  />
                ))}
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
};

export default Books;
