export const FETCH_PRODUCTS = 'FETCH_PRODUCTS';
export const FETCH_PRODUCTS_SUCCESS = 'FETCH_PRODUCTS_SUCCESS';
export const FETCH_ALL_ADMIN_PRODUCTS = 'FETCH_ALL_ADMIN_PRODUCTS';
export const FETCH_ALL_ADMIN_PRODUCTS_SUCCESS =
  'FETCH_ALL_ADMIN_PRODUCTS_SUCCESS';
export const CREATE_OR_MODIFY_PRODUCT = 'CREATE_OR_MODIFY_PRODUCT';
export const DELETE_PRODUCT = 'DELETE_PRODUCT';

export const fetchProducts = () => ({
  type: FETCH_PRODUCTS,
});

export const fetchProductsSuccess = (payload) => ({
  type: FETCH_PRODUCTS_SUCCESS,
  payload,
});

export const fetchAllAdminProducts = (payload) => ({
  type: FETCH_ALL_ADMIN_PRODUCTS,
  payload,
});

export const fetchAllAdminProductsSuccess = (payload) => ({
  type: FETCH_ALL_ADMIN_PRODUCTS_SUCCESS,
  payload,
});

export const createOrModifyProduct = () => ({
  type: CREATE_OR_MODIFY_PRODUCT,
});

export const deleteProduct = (payload) => ({
  type: DELETE_PRODUCT,
  payload,
});
