import React, { useEffect } from 'react';
import Loading from '../../UIElement/Loading/Loading';

import GoodiesItemList from './GoodiesItemList';
import Pagination from '../../UIElement/Pagination/PaginationContainer';

import LogoFantasy from '../../../assets/img/collection_fantasy.png';
import LogoJeunesse from '../../../assets/img/collection_jeunesse.png';
import LogoSF from '../../../assets/img/collection_sf.png';
import LogoUrban from '../../../assets/img/collection_urban.png';

import './stylesGoodies.scss';

const Goodies = ({
  fetchGoodies,
  goodies,
  fetchTypes,
  types,
  fetchCollections,
  collections,
  errorMessage,
}) => {
  useEffect(() => {
    fetchGoodies();
    fetchTypes();
    fetchCollections();
  }, [fetchGoodies, fetchTypes, fetchCollections]);


  const getCollectionPicture = (itemIdCollection) => {
    const collection = collections.find(
      (collection) => collection.id === itemIdCollection
    );

    if (collection !== undefined) {
      return `https://loupargent-oclock.fr/img/${collection.picture_file}`;
    }
  };

  const getCollectionName = (itemIdCollection) => {
    const collection = collections.find(
      (collection) => collection.id === itemIdCollection
    );

    if (collection !== undefined) {
      return collection.label;
    }
  };

  const getTypeName = (itemIdType) => {
    const type = types.find((type) => type.id === itemIdType);

    if (type !== undefined) {
      return type.label;
    }
  };

  if (goodies.length === 0) {
    return <Loading />;
  } else {
    return (
      <div>
        <div className="filter-zone">
          <h3>Filtrer par : </h3>

          <div className="filters">
            <p className="filter">
              <span>COLLECTION : </span>
              <img className="gender-logo" src={LogoFantasy} alt="" />
              <img className="gender-logo" src={LogoJeunesse} alt="" />
              <img className="gender-logo" src={LogoSF} alt="" />
              <img className="gender-logo" src={LogoUrban} alt="" />
            </p>

            <p className="filter">
              <span>TYPE : </span>
              <img className="gender-logo" src={LogoFantasy} alt="" />
              <img className="gender-logo" src={LogoJeunesse} alt="" />
              <img className="gender-logo" src={LogoSF} alt="" />
              <img className="gender-logo" src={LogoUrban} alt="" />
            </p>
          </div>
        </div>

        <div className="goodies">
          <Pagination
            data={goodies}
            Component={GoodiesItemList}
            getCollectionName={getCollectionName}
            getCollectionPicture={getCollectionPicture}
            getTypeName={getTypeName}
          />
        </div>
      </div>
    );
  }
};

export default Goodies;
